var findCacheDir = require('find-cache-dir');
var Git = require('./git.js');
var filenamify = require('filenamify');
var copy = require('./util.js').copy;
var getUser = require('./util.js').getUser;
var fs = require('fs-extra');
var globby = require('globby');
var path = require('path');
var util = require('util');
var log = util.debuglog('gh-pages');

/**
 * Get the cache directory.
 * @param {string} [optPath] Optional path.
 * @return {string} The full path to the cache directory.
 */
function getCacheDir(optPath) {
  var dir = findCacheDir({
    name: 'gh-pages'
  });
  if (!optPath) {
    return dir;
  }
  return path.join(dir, filenamify(optPath));
}
exports.getCacheDir = getCacheDir;
function getRepo(options) {
  if (options.repo) {
    return Promise.resolve(options.repo);
  } else {
    var git = new Git(process.cwd(), options.git);
    return git.getRemoteUrl(options.remote);
  }
}
exports.defaults = {
  dest: '.',
  add: false,
  git: 'git',
  depth: 1,
  dotfiles: false,
  branch: 'gh-pages',
  remote: 'origin',
  src: '**/*',
  remove: '.',
  push: true,
  history: true,
  message: 'Updates',
  silent: false
};

/**
 * Push a git branch to a remote (pushes gh-pages by default).
 * @param {string} basePath The base path.
 * @param {object} config Publish options.
 * @param {Function} callback Callback.
 * @return {Promise} A promise.
 */
exports.publish = function publish(basePath, config, callback) {
  if (typeof config === 'function') {
    callback = config;
    config = {};
  }
  var options = Object.assign({}, exports.defaults, config);

  // For backward compatibility before fixing #334
  if (options.only) {
    options.remove = options.only;
  }
  if (!callback) {
    callback = function callback(err) {
      if (err) {
        log(err.message);
      }
    };
  }
  function done(err) {
    try {
      callback(err);
    } catch (err2) {
      log('Publish callback threw: %s', err2.message);
    }
  }
  try {
    if (!fs.statSync(basePath).isDirectory()) {
      var err = new Error('The "base" option must be an existing directory');
      done(err);
      return Promise.reject(err);
    }
  } catch (err) {
    done(err);
    return Promise.reject(err);
  }
  var files = globby.sync(options.src, {
    cwd: basePath,
    dot: options.dotfiles
  }).filter(function (file) {
    return !fs.statSync(path.join(basePath, file)).isDirectory();
  });
  if (!Array.isArray(files) || files.length === 0) {
    done(new Error('The pattern in the "src" property didn\'t match any files.'));
    return;
  }
  var repoUrl;
  var userPromise;
  if (options.user) {
    userPromise = Promise.resolve(options.user);
  } else {
    userPromise = getUser();
  }
  return userPromise.then(function (user) {
    return getRepo(options).then(function (repo) {
      repoUrl = repo;
      var clone = getCacheDir(repo);
      log('Cloning %s into %s', repo, clone);
      return Git.clone(repo, clone, options.branch, options);
    }).then(function (git) {
      return git.getRemoteUrl(options.remote).then(function (url) {
        if (url !== repoUrl) {
          var message = 'Remote url mismatch.  Got "' + url + '" ' + 'but expected "' + repoUrl + '" in ' + git.cwd + '.  Try running the `gh-pages-clean` script first.';
          throw new Error(message);
        }
        return git;
      });
    }).then(function (git) {
      // only required if someone mucks with the checkout between builds
      log('Cleaning');
      return git.clean();
    }).then(function (git) {
      log('Fetching %s', options.remote);
      return git.fetch(options.remote);
    }).then(function (git) {
      log('Checking out %s/%s ', options.remote, options.branch);
      return git.checkout(options.remote, options.branch);
    }).then(function (git) {
      if (!options.history) {
        return git.deleteRef(options.branch);
      } else {
        return git;
      }
    }).then(function (git) {
      if (options.add) {
        return git;
      }
      log('Removing files');
      var files = globby.sync(options.remove, {
        cwd: path.join(git.cwd, options.dest)
      }).map(function (file) {
        return path.join(options.dest, file);
      });
      if (files.length > 0) {
        return git.rm(files);
      } else {
        return git;
      }
    }).then(function (git) {
      log('Copying files');
      return copy(files, basePath, path.join(git.cwd, options.dest)).then(function () {
        return git;
      });
    }).then(function (git) {
      return Promise.resolve(options.beforeAdd && options.beforeAdd(git)).then(function () {
        return git;
      });
    }).then(function (git) {
      log('Adding all');
      return git.add('.');
    }).then(function (git) {
      if (!user) {
        return git;
      }
      return git.exec('config', 'user.email', user.email).then(function () {
        if (!user.name) {
          return git;
        }
        return git.exec('config', 'user.name', user.name);
      });
    }).then(function (git) {
      log('Committing');
      return git.commit(options.message);
    }).then(function (git) {
      if (options.tag) {
        log('Tagging');
        return git.tag(options.tag).catch(function (error) {
          // tagging failed probably because this tag alredy exists
          log(error);
          log('Tagging failed, continuing');
          return git;
        });
      } else {
        return git;
      }
    }).then(function (git) {
      if (options.push) {
        log('Pushing');
        return git.push(options.remote, options.branch, !options.history);
      } else {
        return git;
      }
    }).then(function () {
      return done();
    }, function (error) {
      if (options.silent) {
        error = new Error('Unspecified error (run without silent option for detail)');
      }
      done(error);
    });
  });
};

/**
 * Clean the cache directory.
 */
exports.clean = function clean() {
  fs.removeSync(getCacheDir());
};